.container {
  display: -webkit-flex;
  display: flex;
}
.container .nav-left {
  background-color: #001529;
  color: #ffffff;
  height: calc(100vh);
}
.container .main {
  -webkit-flex: 1 1;
          flex: 1 1;
  background-color: #f1f3f5;
}
.container .content {
  position: relative;
  padding: 15px;
  height: calc(100vh - 105px);
  overflow-y: auto;
}
.operate-wrap {
  margin-top: 5px;
}
.operate-wrap button {
  margin-right: 10px;
}
.content-wrap {
  background: #ffffff;
  border: 1px solid #e8e8e8;
  margin-top: -3px;
}
.content-wrap .ant-table-wrapper {
  margin-left: -1px;
  margin-right: -2px;
}
